import { RatingInterface } from './../../shared/interfaces/ratings.interface';
import { SocialNewsInterface } from './../../shared/interfaces/social-news.interface';
import { GameWeekInterface } from './../../shared/interfaces/game-week.interface';
import { GamesService } from './../../shared/services/games.service';
import { Observable } from 'rxjs';
import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';

@Component({
    selector: 'gamer-home-page',
    templateUrl: './home-page.component.html',
    styleUrls: ['./home-page.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HomePageComponent implements OnInit {

    constructor(private readonly gamesService: GamesService) { }

    weekGames$: Observable<GameWeekInterface[] | null>;

    socialNews$: Observable<SocialNewsInterface[] | null>;

    icons: any;

    ratings$: Observable<RatingInterface[] | null>;

    eyeIcon = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/a9eed108e15f25ae2b7e0d13aaf38330/eye.png';

    chatIcon = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/a3fd5d48e0103b3c7d442f281a0b4c46/chat-1.png';

    logo = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/c3f451eb255a00765bfb7d8a1841176c/logo.png';

    xboxIcon = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/006b749a5487887542ed06dea33c6ac1/xbox-social-outline-logo.png';

    playstationIcon = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/094c865779eb979a0b57823b7c43d46d/play-station-logo.png';

    pcIcon = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/05ad00db2266e170cce29c99f7246f90/pc-computer-with-monitor_icon-icons.com_56249.png';

    messageIcon = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/0f3077a59d003927e389111d9decad61/envelope.png';

    phoneIcon = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/dbe6aa39bc662271f3bbf4ad335ad043/mobile-phone-2_icon-icons.com_56265.png';

    searchIcon = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/6c118b329de255c5ad8789ed036f29fb/search.png';

    avatarIcon = 'https://trello-attachments.s3.amazonaws.com/5c1d66487ffd5b268e26115c/5d17561394e0524688cacfd6/ff6f5848efff384f68793ee142f1a4cf/avatar.png';

    ngOnInit(): void {
        this.weekGames$ = this.gamesService.getWeekGames$();
        this.socialNews$ = this.gamesService.getSocialNews$();
        this.ratings$ = this.gamesService.getRatings$();
    }

    /* создает маску для заднего фона в виде картинки с установленным цветом */
    setMaskBackGroundWithColor(url: string, position: any, size: any, color: any) {
        const customStyle = {
            '-webkit-mask-image': `url('${url}')`,
            '-webkit-mask-position': position,
            '-webkit-mask-size': size,
            'background-color': color
        };

        return customStyle;
    }

    /* заменяет задний фон элемента на картинку */
    setBackgroudImage(url: string, position: any, size: any) {
        const customStyle = {
            'background-image': `url('${url}')`,
            'background-position': position,
            'background-size': size,
            'background-repeat': 'no-repeat'
        };

        return customStyle;
    }
}
