import { RatingInterface } from './../interfaces/ratings.interface';
import { SocialNewsInterface } from './../interfaces/social-news.interface';
import { catchError } from 'rxjs/operators';
import { GameWeekInterface } from './../interfaces/game-week.interface';
import { Observable, of } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class GamesService {

    constructor(private httpClient: HttpClient) { }


    public getWeekGames$(): Observable<GameWeekInterface[] | null> {
        return this.httpClient.get<GameWeekInterface[]>('../../../../../assets/data/gamer/json/game-weeks.json')
            .pipe(
                catchError(() => {
                    return of(null);
                })
            );
    }

    public getSocialNews$(): Observable<SocialNewsInterface[] | null> {
        return this.httpClient.get<SocialNewsInterface>('../../../../../assets/data/gamer/json/social-news.json')
            .pipe(
                catchError(() => {
                    return of(null);
                })
            );
    }

    public getRatings$(): Observable<RatingInterface[] | null> {
        return this.httpClient.get<RatingInterface>('../../../../../assets/data/gamer/json/ratings.json')
            .pipe(
                catchError(() => {
                    return of(null);
                })
            );
    }
}
